import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, Btn } from 'components/common/button';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message, Checkbox } from 'antd';
import { getDocOut, deleteDocOut, downloadFile } from 'services/docOut';
import { getFile } from 'services/fileStorage';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import FormFilterDocOut from './FormFilterDocOut';

class DocOutList extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteDocOut(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportDocOut = () => {
    const { paramSearch } = this.state;
    downloadFile(paramSearch);
  };

  exportUser = record => {
    getFile(record.attachmentId);
  };

  render() {
    const { reload, paramSearch } = this.state;
    const columns = [
      {
        title: 'Ngy i',
        dataIndex: 'docOutDate',
        key: 'docOutDate',
        render: (text, record) => <div>{dateToString(record.docOutDate)}</div>,
      },
      {
        title: 'n v gi',
        dataIndex: 'sentUnit',
        key: 'sentUnit',
      },
      {
        title: 'S CV',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'X l s CV n',
        dataIndex: 'docInId',
        key: 'docInId',
      },
      {
        title: 'Ngy vo s',
        dataIndex: 'noteDate',
        key: 'noteDate',
        render: (text, record) => <div>{dateToString(record.noteDate)}</div>,
      },
      {
        title: 'Ni dung',
        dataIndex: 'docContent',
        key: 'docContent',
      },
      {
        title: ' gi email',
        dataIndex: 'isSentEmail',
        key: 'isSentEmail',
        render: (text, record) => <Checkbox checked={record.isSentEmail} />,
      },
      {
        title: 'Ni nhn',
        dataIndex: 'recipientPlace',
        key: 'recipientPlace',
      },
      {
        title: 'Ni lu',
        dataIndex: 'savedPlace',
        key: 'savedPlace',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <ColEditDelete
              linkEdit={{
                to: {
                  pathname: routeConstant.TO_CHUC__CONG_VAN__CONG_VAN_DI__NHAP_DU_LIEU,
                  search: `?id=${record.id}`,
                },
              }}
              onConfirmDelete={() => this.handleDelete(record.id)}
            />
            {record.attachmentId && (
              <Btn style={{ width: 25 }} onClick={() => this.exportUser(record)} icon="download" />
            )}
          </Fragment>
        ),
      },
    ];
    return (
      <Card title="Nhp k lut" className="doc-out">
        <Row>
          <Col span={24}>
            <FormFilterDocOut />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <div className="btn-group" style={{ justifyContent: 'flex-start' }}>
              <Link to={routeConstant.TO_CHUC__CONG_VAN__CONG_VAN_DI__NHAP_DU_LIEU}>
                <BtnAddNew />
              </Link>
              <Btn onClick={this.exportDocOut} icon="download">
                Download
              </Btn>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getDocOut}
              paramSearch={paramSearch}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DocOutList />
  </Page>
);



